.promotions-section {
    width: 100%;
    position: relative;
    padding: 85px 0 0;
}

.promotions__wrapper {
    padding: 30px 70px;
    width: 100%;
    height: auto;
    background: #0273BA;
    border-radius: 27px;
}

.pro__wrapper-title {
    margin: 0 !important;
}

.pro__wrapper-title p {
    margin: 0;
    font-family: Montserrat;
    font-style: normal;
    font-weight: bold;
    font-size: 30px;
    line-height: 46px;
    letter-spacing: -1px;
    color: #FFFFFF;
}

.pro__wrapper-title span {
    font-family: Montserrat;
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 34px;
    letter-spacing: -1px;
    color: #FFFFFF;
}

.pro__wr-text p {
    font-family: Montserrat;
    font-style: normal;
    font-weight: bold;
    font-size: 30px;
    line-height: 46px;
    letter-spacing: -1px;

    color: #FFFFFF;

}

.icon__promotions {
    position: absolute;
    left: -15px;
    bottom: -5px;
}

.promo__days p {
    font-family: Montserrat;
    font-style: normal;
    font-weight: bold;
    font-size: 80px;
    line-height: 80px;
    display: flex;
    align-items: center;
    letter-spacing: -1px;
    /* padding-top: 10px; */
    /* White Color Armon */
    margin: 0 !important;
    color: #FFFFFF;
}

.promo__days span {
    font-family: Montserrat;
    font-style: normal;
    font-weight: normal;
    font-size: 26px;
    line-height: 49px;
    /* identical to box height */

    text-align: center;
    letter-spacing: -1px;

    /* White Color Armon */

    color: #FFFFFF;

}

.icon__promotions img {
    width: 398px;
}

.btn__promo {
    margin-top: 20px;
    display: flex;
    justify-content: center;
}

.btn__promo button {
    background: #0273BA;
    border-radius: 27px;
    outline: none;
    border: 2px solid #fff;
    font-family: Montserrat;
    font-style: normal;
    font-weight: 600;
    font-size: 22px;
    line-height: 27px;
    display: flex;
    align-items: center;
    text-align: center;
    letter-spacing: -1px;
    color: #FFFFFF;
    padding: 5px 20px 5px 20px;
}

.btn__promo button:hover {
    background: #FF0000;
}

/* start modal */

.promo__modal .rodal-fade-enter .rodal-dialog {
    background: #0273BA;
    border-radius: 27px;
    width: 520px !important;
    height: 715px !important;
    padding: 35px !important;
}

.modal__input__group .toast.show {
    position: absolute;
    bottom: 344px;
    display: block;
    opacity: 1;
}

.modal__input__group .toast.show .toast-body {
    padding: .5rem;
}

.modal__input__group .toast {
    background-color: #fff !important;
}

.toasp__text-modal p {
    font-family: Montserrat;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 29px;
    text-align: center;
    letter-spacing: -1px;
    margin: 0 !important;
    padding: 0 !important;
    color: rgb(109, 109, 109);

}

.promo__modal .rodal-fade-enter .rodal-dialog .rodal-close {
    border-radius: 50% !important;
    top: 20px !important;
    right: 20px !important;
    width: 30px !important;
    height: 30px !important;
    border: 2px solid #fff !important;
}

.promo__modal .rodal-fade-enter .rodal-dialog .rodal-close:before {
    border-radius: 0 !important;
    background: #fff !important;
    width: 60% !important;
    left: 5px !important;
}

.promo__modal .rodal-fade-enter .rodal-dialog {
    border-radius: 16px !important;
    animation-duration: 300ms;
}

.promo__modal .rodal-fade-enter .rodal-dialog .rodal-close:after {
    border-radius: 0 !important;
    background: #fff !important;
    width: 60% !important;
    left: 5px !important;
}

.modal-icon__promotions {
    display: flex;
    justify-content: center;
}

.modal-icon__promotions img {
    position: absolute;
    width: 320px;
    bottom: 490px;
}

.stock__wrapper {
    margin-top: 185px;
}

.stock__wrapper-title p {
    font-family: Montserrat;
    font-style: normal;
    font-weight: bold;
    font-size: 31px;
    line-height: 49px;
    text-align: center;
    letter-spacing: -1px;

    /* White Color Armon */

    color: #FFFFFF;

}

.pdtext {
    display: flex;
    justify-content: center;
    width: 100%;
}

.ddtt {
    width: 80px;
    height: 3px;
    background: #FFFFFF;
    border-radius: 15px;
    margin-bottom: 15px;
}

.stock__wrapper-text p {
    font-family: Montserrat;
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 34px;
    letter-spacing: -1px;

    /* White Color Armon */

    color: #FFFFFF;
}

.modal__input__group ul {
    padding: 0 !important;
}

.modal__input__group ul li {
    list-style: none;
    display: flex;
    justify-content: center;
}

.btn__stock {
    display: flex;
    justify-content: center;
}

.modal__input__group .mdl__input {
    background: #FFFFFF;
    border-radius: 29px;
    outline: none;
    border: none;
    height: 40px;
    width: 60%;
    padding-left: 15px;
}

